import React, { useState } from 'react';
import { Button, Modal, Space } from 'antd';

import { GoodsCategorySelect } from '../base_components/goods_category/goods_category_select';

const GoodsCategoryModal = ({visible, setVisible, visibleNum, goodsObj, updateCategory}) => {
    // 商品分类选择对话框
    const [categorySelectors, setCategorySelectors] = useState('');

    const confirmSelect = () => {
        // 确认选择分类(热搜词)
        updateCategory(categorySelectors);
        setVisible(0);
    }

    const categorySelectorsChange = (selectedCategories) => {
        // 选择框更新后操作
        setCategorySelectors(selectedCategories);
    }

    return (
        <>
            <Modal visible={visible === visibleNum}
                title="选择分类(热搜词)"
                mask={true}
                closable={true}
                centered={true}
                footer={[
                    <Button key="categoryConfirm" onClick={confirmSelect}>确认</Button>,
                    <Button key="categoryCancle" onClick={() => { setVisible(0); }}>取消</Button>
                ]}
            >
                <Space direction="vertical">
                    <p>商品: {goodsObj.name}</p>
                    <GoodsCategorySelect
                        visible={visible}
                        onChange={categorySelectorsChange}
                        goodsObj={goodsObj}
                    ></GoodsCategorySelect>
                </Space>
            </Modal>
        </>
    );
}
GoodsCategoryModal.defaultProps = {
    visible: 0,
    setVisible: visibleNum => console.log(visibleNum),
    visibleNum: 1,
    goodsObj: {},
    updateCategory: categorySelectors => console.log(categorySelectors),
}

export { GoodsCategoryModal };
