import React from 'react';

import AxiosRequest from '../../apis';
import { sendAxiosRequest } from '../../apis/sendRequest';

const  apiLists = ['category-admin/v1/category', ];

// goods category requests
const ComponentCategoryRequestGet = ({ requestCount, reqParams, responseDataHandle }) => {
    // Deliver Get请求
    const api = [].concat(apiLists)[0];
    const method = 'get';
    const reqObject = {};
  
    return (
      <AxiosRequest
        responseDataHandle={responseDataHandle} api={api} method={method}
        reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
      ></AxiosRequest>
    );
};
ComponentCategoryRequestGet.defaultProps = {
    requestCount: 0,
    reqParams: {},
    responseDataHandle: (respObj) => { console.log(respObj); },
};

const categoryRequestGet = () => {
    // 分类 Get请求
    return new Promise(function (resolve, reject) {
        const api = apiLists[0];
        const method = 'get';
        const reqParams = {};
        const reqObject = {};
        sendAxiosRequest(api, method, reqParams, reqObject)
            .then(respObj => {
            resolve(respObj);
            })
            .catch(err => {
            console.log(err);
            reject(err);
            });
    });
}

export { ComponentCategoryRequestGet, categoryRequestGet };