import React, { useState, useEffect, useRef } from 'react';

import { CustomList } from '../../base_components/lists/custom_list';
import { GoodsRequestGet, GoodsRequestPut } from './goods_requests';
import UpdateGoods from './update_goods';
import { renderItem, judgeHasMore } from './goods_columns_label_config';
import { LoadMoreButton } from '../../base_components/load_more_button_conpoment';
import { GoodsCategoryModal } from '../../modals/goods_category_opt';
import { ScrollGoBack } from '../../base_components/scroll_handle/goback_origin_scroll';
// 列表编辑 页面

const updateGoodsData = (originGoodsData, updateObj) => {
    // 更新更新过的商品
    const copyGoodsData = [].concat(originGoodsData)
    const updateGoodsId = updateObj.id
    for (let index = 0; index < copyGoodsData.length; index++) {
        const element = copyGoodsData[index];
        if (element.id === updateGoodsId) {
            if (Object.keys(updateObj).includes('del_handle') && updateObj.del_handle) {
                // 删除操作
                copyGoodsData.splice(index, 1);
            } else {
                copyGoodsData[index] = updateObj
            }
            return copyGoodsData
        }
    }
    return copyGoodsData
}

const GoodsList = ({ reloadListCount, setReloadListCount, requestParams, selectButtonOnClick }) => {

    const [visible, setVisible] = useState(0);

    const [hasMoreState, setHasMoreState] = useState(true);
    const [loading, setLoading] = useState(false);
    // 设置为true 开始request请求, 解决useState异步
    const [requesting, setRequesting] = useState(false);
    const reqParams = useRef({ page: 0 });

    const [goodsData, setGoodsData] = useState([]);
    const [goodsRequestGetCount, setGoodsRequestGetCount] = useState(0);

    const [goodsRequestPutCount, setGoodsRequestPutCount] = useState(0);
    const [goodsPutObj, setGoodsPutObj] = useState({});


    const [selectedObj, setSelectedObj] = useState({});

    useEffect(() => {
        if (hasMoreState && !loading && requesting) {
            // 使用useEffect 触发request 解决 useState 异步
            loadMore();
            setRequesting(false);
        }
    }, [requesting, hasMoreState, loading])

    useEffect(() => {
        // 加载、更新或创建数据后 重新读取列表
        reqParams.current = { ...requestParams };
        reloadListsHandle();
    }, [reloadListCount]);

    useEffect(() => {
        // goodsPutObj 更新后判断是否发送更新分类的请求
        if (Object.keys(goodsPutObj).includes('categories')) {
            setGoodsRequestPutCount(goodsRequestPutCount + 1)
        }
    }, [goodsPutObj])

    const loadMore = () => {
        // 读取更多列表
        if (hasMoreState && !loading) {
            setLoading(true);
            setHasMoreState(false);
            const current_page = Number.isInteger(reqParams.current.page) ? reqParams.current.page + 1 : 1;
            reqParams.current = { ...reqParams.current, page: current_page };
            console.log(reqParams.current);
            setGoodsRequestGetCount(goodsRequestGetCount + 1);
        } else {
            console.log('Error state: hasMore is ', hasMoreState, ', loading is ', loading);
        }
    };

    const reloadListsHandle = () => {
        // 重新读取数据
        reqParams.current = { ...reqParams.current, page: 0 };
        // console.log(reqParams.current);
        setGoodsData([]);
        setHasMoreState(true);
        setLoading(false);
        setRequesting(true);    // useEffect 触发request请求，解决useState异步
    };

    const goodsGetResponse = (resp) => {
        // console.log('resp process...');
        setLoading(false);
        if (Object.keys(resp).includes('code')
            && resp.code === 1000
            && Object.keys(resp).includes('results')
        ) {
            setGoodsData(goodsData.concat(resp.results));
            setHasMoreState(judgeHasMore(resp));   // 判断是否还有更多
        } else {
            console.log('Error: get data...');
        }
    };

    const updateGoodsCategory = (selectorCategories) => {
        // 更新商品分类
        console.log(selectorCategories);
        const updateGoodsCategoryObj = {categories: selectorCategories.join('-')}
        setGoodsPutObj(updateGoodsCategoryObj); // goodsPutObj 更新后触发 useEffect
    }

    const updatedGoodsHandle = (resp) => {
        // 更新结果
        console.log(resp);
        setGoodsData(updateGoodsData(goodsData, resp))
    }

    return (
        <div>
            <ScrollGoBack visible={visible}></ScrollGoBack>

            <CustomList
                header="Goods List"
                listData={goodsData}
                visible={visible}
                setVisible={setVisible}
                randerItem={renderItem}
                setSelectedItem={setSelectedObj}
                selectButtonOnClick={selectButtonOnClick}
            ></CustomList>

            <UpdateGoods
                visible={visible}
                setVisible={setVisible}
                oldObj={selectedObj}
                reloadListCount={reloadListCount}
                setReloadListCount={setReloadListCount}
                updatedHandle={updatedGoodsHandle}
            ></UpdateGoods>

            <GoodsCategoryModal
                visible={visible}
                setVisible={setVisible}
                visibleNum={3}
                goodsObj={selectedObj}
                updateCategory={updateGoodsCategory}
            ></GoodsCategoryModal>

            <GoodsRequestPut
                requestCount={goodsRequestPutCount}
                reqObject={goodsPutObj}
                responseDataHandle={updatedGoodsHandle}
                id={selectedObj.id}
            ></GoodsRequestPut>

            <GoodsRequestGet requestCount={goodsRequestGetCount}
                reqParams={reqParams.current}
                responseDataHandle={goodsGetResponse}></GoodsRequestGet>

            <LoadMoreButton hasMore={hasMoreState} onClick={loadMore}></LoadMoreButton>
        </div>
    );
}

GoodsList.defaultProps = {
    reloadListCount: 1,
    setReloadListCount: (addedNum) => { console.log(addedNum); },
    requestParams: { page: 0 },
    selectButtonOnClick: null,
}

export default GoodsList;
