import React, { useEffect, useState } from 'react';
import { Select } from 'antd';
// mode="multiple"
// import { CustomSelect } from '../select_component';
import { categoryRequestGet } from './goods_category_requests';


// 商品分类(热搜词)编辑
const GoodsCategorySelect = ({visible, goodsObj, onChange}) => {
    const [options, setOptions] = useState([]);

    const [selector, setSelector] = useState([]);

    useEffect(() => {
        // 初始化分类选择
        categoryRequestGet().then(respObj => {
            const channelsList = respObj.results;
            let updateOptions = [];
            for (let index = 0; index < channelsList.length; index++) {
                const element = channelsList[index];
                updateOptions.push({
                    value: element.id,
                    label: element.category_name
                })
            }
            setOptions(updateOptions);
        }).catch(error => {
            console.log(error);
            setOptions([]);
        });
    }, []);

    useEffect(() => {
        // 商品更改后给选择框赋值
        if (visible < 1) {
            return;
        }
        console.log(goodsObj);
        let categories = goodsObj.categories ? goodsObj.categories : '';
        categories = categories.split('-');
        for (let index = categories.length - 1; index > -1; index--) {
            // 删除空值
            if (categories[index] === '') {
                categories.splice(index, 1)
            }else{
                categories[index] = parseInt(categories[index]);
            }
        }
        console.log(categories);
        setSelector(categories);
    }, [visible]);

    const selecteHandle = (values) => {
        console.log(`selected ${values}`);
        setSelector(values);
        onChange(values);
    }

    return (<Select
            style={{ width: '100%' }}
            options={options}
            value={selector}
            onChange={selecteHandle}
            mode="multiple"
        ></Select>)
}
GoodsCategorySelect.defaultProps = {
    visible: 0,
    goodsObj: {},
    onChange: (selectors) => console.log(selectors),
}

export { GoodsCategorySelect };
